const canUsePartnersVacancy = (status) => {
  switch (status) {
    case 'Преакселерация':
      return true
    case 'Акселерация':
      return true
    case 'Испытательный срок':
      return true
    case 'Сообщество':
      return true
    default:
      return false
  }
}

export default canUsePartnersVacancy
